


.container {
    background-image: url('./assests/img/bg-01.jpg');
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    height: auto;
}

.wrap-login {
    width: 500px;
    background: #fff;
    border-radius: 10px;
    padding: 65px 55px 54px 55px;
}

.header {
    display: block;
    font-family: Poppins-Bold;
    font-size: 39px;
    color: #333;
    line-height: 1.2;
    text-align: center;
    padding-bottom: 49px;
}
.wrap-inp {
    position: relative;
    border-bottom: 2px solid #d9d9d9;
}

.input {
    font-family: Poppins-Medium;
    font-size: 16px;
    color: #333;
    line-height: 1.2;
    display: block;
    width: 100%;
    height: 55px;
    background: 0 0;
    padding: 0 7px 0 43px;
}

.user-label {
    font-family: Poppins-Regular;
    font-size: 14px;
    color: #333;
    line-height: 1.5;
    padding-left: 7px;
}

.icon {
    position: absolute;
    bottom: 16px;
    left: 14px;
}
.register {
    padding: 8px 0 31px 0;
    a {
        font-family: Poppins-Regular;
        font-size: 14px;
        line-height: 1.7;
        color: #666;
        &:hover {
            color: #a64bf4;
        }
    }
}
.submit-btn {
    width: 100%;
    display: block;
    position: relative;
    z-index: 1;
    border-radius: 25px;
    overflow: hidden;
    margin: 0 auto;
    button {
        font-family: Poppins-Medium;
        font-size: 16px;
        color: #fff;
        line-height: 1.2;
        display: flex;
        justify-content: center;
        align-items: center;
        padding: 0 20px;
        width: 100%;
        height: 50px;
        background: -webkit-linear-gradient(right,#00dbde,#fc00ff,#00dbde,#fc00ff);
        font-weight: 600;
        cursor: pointer;
        &:hover{
            opacity: 0.6;
        }
    }
}


@media screen and (min-height :768px) {
    .container{
        height: 1400px;
    }
}
@media screen and (max-height :767px) {
    .container{
        height: 767px;
    }
}